.folder {
    width: 928px;
    height: 1150px;
    background: url('../../images/folder-bg.png');
    margin-top: 40px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
}

.file__inner {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.print {
    width: 755px;
    height: 1060px;
    background-color: white;
    margin-left: 60px;
    margin-top: 55px;
    transform: rotate(-3deg);
    padding: 10px;
    box-sizing: border-box;
    box-shadow: -11px -11px 16px -8px rgba(0, 0, 0, 0.82);
    -webkit-box-shadow: -11px -11px 16px -8px rgba(0, 0, 0, 0.82);
    -moz-box-shadow: -11px -11px 16px -8px rgba(0, 0, 0, 0.82);
}